import React, { memo } from "react";
import {
  AccumulativeShadows,
  RandomizedLight,
  Environment as EnvironmentImpl
} from "@react-three/drei";
import { Vector3 } from "three";

interface EnvironmentProps {
  direction?: [number, number, number] | Vector3;
}

export const Environment: React.FC<EnvironmentProps> = memo(
  ({ direction = new Vector3(5, 5, 5) }) => {
    const position: [number, number, number] = Array.isArray(direction)
      ? direction
      : [direction.x, direction.y, direction.z];

    return (
      <>
        <directionalLight
          position={position}
          intensity={0.5}
          shadow-mapSize={1024}
          castShadow
        />
        <directionalLight
          position={[-5, 5, 5]}
          intensity={0.1}
          shadow-mapSize={128}
          castShadow
        />
        <directionalLight
          position={[-5, 5, -5]}
          intensity={0.1}
          shadow-mapSize={128}
          castShadow
        />
        <directionalLight
          position={[0, 5, 0]}
          intensity={0.1}
          shadow-mapSize={128}
          castShadow
        />
        <AccumulativeShadows
          frames={100}
          alphaTest={0.85}
          opacity={0.75}
          scale={30}
          position={[0, -1.5, 0]}
        >
          <RandomizedLight
            amount={8}
            radius={2.5}
            ambient={0.5}
            intensity={1}
            position={position}
            bias={0.001}
          />
        </AccumulativeShadows>
        <EnvironmentImpl preset="city" />
      </>
    );
  }
);
