import React, { useEffect, useState } from "react";
import { EventData } from "./EventData";

export const WpfHandler: React.FC = () => {
  const [block, setBlock] = useState<EventData | null>(null);

  // Function to send a message to the WPF application
  const sendMessageToWpf = () => {
    if (window.chrome && window.chrome.webview) {
      const message: EventData = {
        type: "graded",
        value: true,
        object: "that's a graded block lots"
      };
      window.chrome.webview.postMessage(JSON.stringify(message));
    } else {
      console.warn("WebView2 is not available");
    }
  };

  async function getBlockDetails(webview: any): Promise<EventData> {
    try {
      const blockProxy = await webview.hostObjects.block;
      const id = await blockProxy.id;
      const name = await blockProxy.name;
      const numberOfLots = await blockProxy.numberOfLots;
      const lotIds = await blockProxy.GetLotIds();

      return { type: id, value: numberOfLots > 0, object: lotIds };
    } catch (error) {
      console.error("Failed to get block details:", error);
      throw error;
    }
  }

  useEffect(() => {
    if (window.chrome && window.chrome.webview) {
      getBlockDetails(window.chrome.webview)
        .then(blockDetails => {
          setBlock(blockDetails);
        })
        .catch(error => {
          console.error("Error setting block details:", error);
        });
    }
  }, []);

  // Listen for messages from the WPF application
  useEffect(() => {
    const handleBackendEvent = (event: MessageEvent) => {
      try {
        const isString = typeof event.data === typeof "string";
        const message: EventData = isString
          ? event.data
          : JSON.parse(event.data);
        // Process the received message from the WPF app
        alert(`Received message from WPF: ${message}`);
      } catch (error) {
        console.error("Error parsing message from WPF:", error);
      }
    };

    if (window.chrome && window.chrome.webview) {
      window.chrome.webview.addEventListener("message", handleBackendEvent);
      return () => {
        window.chrome?.webview?.removeEventListener(
          "message",
          handleBackendEvent
        );
      };
    } else {
      console.warn("WebView2 is not available");
    }
  }, []);

  return (
    <div>
      <h1>React App</h1>
      <button onClick={sendMessageToWpf}>Send Message to WPF</button>
      {block && (
        <div>
          <h2>Block Details</h2>
          <p>Type: {block.type}</p>
          <p>Value: {block.value ? "True" : "False"}</p>
          <p>Object: {block.object}</p>
        </div>
      )}
    </div>
  );
};
